.calculator {
    display: flex;
    align-items: center;
    font-family: var(--sans-font);
    margin: 1rem 0;
}
.calculator input {
    font-family: inherit;
    font-size: 1rem;
    width: 50%;
    max-width: 25ch;
}
.result {
    background-color: var(--col-prim);
    margin: 0 1rem;
    padding: 0.1rem 0.5rem;
    font-family: var(--mono-font);
    font-size: 0.9rem;
    border-radius: 0.5rem;
    max-width: 50%;
    overflow: auto;
}